
@import './colors.scss';

.app {
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
  color: $clr-fg;
  background-color: $clr-bg;
  padding-bottom: 6em;

  .light {
    $clr-bg: #fcfcfc;
    $clr-bg-alt: #fff;
    $clr-fg: #555;
    $clr-fg-alt: #444;
    $clr-primary: #2978b5;
    $shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }
}
